import Thread3 from '../thread3';
import Worker from './thread4.1.worker.js';



// TODO: remove class name?
export default class Thread extends Thread3
{
	// TODO: add stack_size option.
	constructor (wasm_wrapper)
	{
		super(wasm_wrapper);

		this.exports = {};

		Object.keys(wasm_wrapper.exports).forEach
		(
			export_name =>
			{
				if (typeof wasm_wrapper.exports[export_name] === 'function' && !export_name.startsWith('__'))
				{
					this.exports[export_name] = (...args) => this.work(export_name, args);
				}
			},
		);
	}
}
