export default function ()
{
	const that = this;



	this.options =
		Object.assign
		(
			{
				// 1024 b * 1024 b == 1048576 b == 1 mb
				thread_stack_size: 0x0100000,
			},

			this.options,
		);



	[
		'Uint8Array',
		'Int8Array',
		'Uint16Array',
		'Int16Array',
		'Uint32Array',
		'Int32Array',
		'Uint8Array',
		'Int8Array',
		'BigUint64Array',
		'BigInt64Array',
		'Float32Array',
		'Float64Array',
	]
		.forEach
		(
			typed_array_name =>
			{
				if (self[typed_array_name])
				{
					/**
					 * byteOffset is an address in WASM memory.
					 *
					 * Array has to be used carefully, especially on mobile devices.
					 * Undefined behavior may be occured in the following cases:
					 * - Transferring array between workers (for both ArrayBuffer and SharedArrayBuffer).
					 * - Calling slice().
					 * ...
					 */

					this[typed_array_name] = class extends self[typed_array_name]
					{
						constructor (...args)
						{
							if (args[0] instanceof self.ArrayBuffer || (self.SharedArrayBuffer && args[0] instanceof self.SharedArrayBuffer))
							{
								if (args[0] === that.memory.buffer)
								{
									super(...args);

									this.__allocated = false;

									return;
								}

								throw new Error('wasm-wrapper typed array can not be backed by array buffer other than wasm-wrapper instance memory buffer.');
							}

							let length = 0;

							if (typeof args[0] === 'object')
							{
								// length = args[0].length;
								[ { length } ] = args;

								if (args[0] instanceof that[typed_array_name] || args[0] instanceof that[`${ typed_array_name }2`])
								{
									const [ { byteOffset } ] = args;

									super(that.memory.buffer, byteOffset, length);

									this.__allocated = false;
								}
								else
								{
									const byteOffset = that.exports.__malloc(length * self[typed_array_name].BYTES_PER_ELEMENT);

									super(that.memory.buffer, byteOffset, length);

									this.__allocated = true;

									this.set(args[0]);
								}
							}
							else
							{
								length = args[0];

								const byteOffset = that.exports.__malloc(length * self[typed_array_name].BYTES_PER_ELEMENT);

								super(that.memory.buffer, byteOffset, length);

								this.__allocated = true;
							}
						}

						__free () { that.exports.__free(this.byteOffset); }

						// slice (...args) {}
					};

					this[`${ typed_array_name }2`] = class
					{
						constructor (...args)
						{
							let byteOffset = 0;
							let length = 0;

							this.__allocated = false;

							if (args[0] instanceof self.ArrayBuffer || (self.SharedArrayBuffer && args[0] instanceof self.SharedArrayBuffer))
							{
								if (args[0] === that.memory.buffer)
								{
									byteOffset = args[1] || 0;
									length = args[2] || (that.memory.buffer.byteLength / self[typed_array_name].BYTES_PER_ELEMENT);

									Object.assign(this, { byteOffset, length });

									return;
								}

								throw new Error('wasm-wrapper typed array can not be backed by array buffer other than wasm-wrapper instance memory buffer.');
							}

							if (typeof args[0] === 'object')
							{
								[ { length } ] = args;

								// if (args[0].buffer === that.memory.buffer)
								if (args[0] instanceof that[typed_array_name] || args[0] instanceof that[`${ typed_array_name }2`])
								{
									[ { byteOffset } ] = args;

									Object.assign(this, { byteOffset, length });
								}
								else
								{
									byteOffset = that.exports.__malloc(length * self[typed_array_name].BYTES_PER_ELEMENT);

									this.__allocated = true;

									Object.assign(this, { byteOffset, length });

									this.set(args[0]);
								}
							}
							else
							{
								[ length ] = args;

								byteOffset = that.exports.__malloc(length * self[typed_array_name].BYTES_PER_ELEMENT);

								this.__allocated = true;

								Object.assign(this, { byteOffset, length });
							}
						}

						get buffer () { return that.memory.buffer; }

						__free () { that.exports.__free(this.byteOffset); }

						__getData () { return new self[typed_array_name](that.memory.buffer, this.byteOffset, this.length); }
					};

					[
						'set',
						'slice',
					]
						.forEach
						(
							function_name =>
							{
								this[`${ typed_array_name }2`].prototype[function_name] = function (...args)
								{
									return (new self[typed_array_name](that.memory.buffer, this.byteOffset, this.length))[function_name](...args);
								};
							},
						);
				}
			},
		);



	{
		this.mem =
		// TODO: wrap into "cxx" prop.
		// TODO: add "views" props instead of using this.memory_views.
		{
			'bool': { kind: 'Uint' },
			'char': { kind: 'Uint' },
			'int': { kind: 'Int' },
			'short': { kind: 'Int' },
			'long': { kind: 'Int' },
			'size_t': { kind: 'Uint' },
			'addr': { kind: 'Uint' },
			'uint8_t': { kind: 'Uint' },
			'int8_t': { kind: 'Int' },
			'uint16_t': { kind: 'Uint' },
			'int16_t': { kind: 'Int' },
			'uint32_t': { kind: 'Uint' },
			'int32_t': { kind: 'Int' },
			'uint64_t': { kind: 'Uint' },
			'int64_t': { kind: 'Int' },
			'float': { kind: 'Float' },
			'double': { kind: 'Float' },
			'long_double': { kind: 'Float' },
			'c_string': {},
		};

		this.type_names = Object.keys(this.mem);

		this.type_names
			.forEach
			(
				(type_name, type_name_index) =>
				{
					const size = this.exports.__sizeof(type_name_index);

					let typed_array_constructor_name = `${ this.mem[type_name].kind }${ size * 8 }Array`;

					if (!this[typed_array_constructor_name])
					{
						typed_array_constructor_name = `Big${ typed_array_constructor_name }`;
					}

					Object.assign
					(
						this.mem[type_name],

						{
							size,
							typed_array_constructor: self[typed_array_constructor_name],
							typed_array_constructor_wasm: this[typed_array_constructor_name],
							typed_array_constructor_wasm2: this[`${ typed_array_constructor_name }2`],
							interp_log: addr => this[type_name](addr)[0],
							interp_thread: addr => this[type_name](addr),
						},
					);
				},
			);

		this.mem.bool.interp_log = addr => Boolean(this.bool(addr)[0]),

		this.mem.c_string.interp_log = addr => this.c_string2(this.addr(addr)[0]);
		this.mem.c_string.interp_thread = addr => this.addr(addr);
		this.mem.c_string.interp_arg = str => (new this.mem.char.typed_array_constructor_wasm(this.constructor.convertDomStringToUint8Array(str))).byteOffset;
	}



	{
		this.memory_views = {};

		this.memory_views.UI8 = new self.Uint8Array(this.memory.buffer);
		this.memory_views.UI8 = new self.Uint8Array(this.memory.buffer);
		this.memory_views.I8 = new self.Int8Array(this.memory.buffer);
		this.memory_views.UI16 = new self.Uint16Array(this.memory.buffer);
		this.memory_views.I16 = new self.Int16Array(this.memory.buffer);
		this.memory_views.UI32 = new self.Uint32Array(this.memory.buffer);
		this.memory_views.I32 = new self.Int32Array(this.memory.buffer);
		this.memory_views.I32 = new self.Int32Array(this.memory.buffer);
		if (self.BigUint64Array) { this.memory_views.UI64 = new self.BigUint64Array(this.memory.buffer); }
		if (self.BigInt64Array) { this.memory_views.I64 = new self.BigInt64Array(this.memory.buffer); }
		this.memory_views.F32 = new self.Float32Array(this.memory.buffer);
		this.memory_views.F64 = new self.Float64Array(this.memory.buffer);
	}
}
