// TODO: new for C++ types (e.g. _new('uint32_t', 16)).

export default
{
	// https://en.cppreference.com/w/cpp/language/types
	bool (addr, length = 1) { return new this.mem.bool.typed_array_constructor(this.memory.buffer, addr, length); },
	char (addr, length = 1) { return new this.mem.char.typed_array_constructor(this.memory.buffer, addr, length); },
	int (addr, length = 1) { return new this.mem.int.typed_array_constructor(this.memory.buffer, addr, length); },
	short (addr, length = 1) { return new this.mem.short.typed_array_constructor(this.memory.buffer, addr, length); },
	long (addr, length = 1) { return new this.mem.long.typed_array_constructor(this.memory.buffer, addr, length); },
	size_t (addr, length = 1) { return new this.mem.size_t.typed_array_constructor(this.memory.buffer, addr, length); },
	addr (addr, length = 1) { return new this.mem.addr.typed_array_constructor(this.memory.buffer, addr, length); },
	uint8_t (addr, length = 1) { return new self.Uint8Array(this.memory.buffer, addr, length); },
	int8_t (addr, length = 1) { return new self.Int8Array(this.memory.buffer, addr, length); },
	uint16_t (addr, length = 1) { return new self.Uint16Array(this.memory.buffer, addr, length); },
	int16_t (addr, length = 1) { return new self.Int16Array(this.memory.buffer, addr, length); },
	uint32_t (addr, length = 1) { return new self.Uint32Array(this.memory.buffer, addr, length); },
	int32_t (addr, length = 1) { return new self.Int32Array(this.memory.buffer, addr, length); },
	uint64_t (addr, length = 1) { return new self.BigUint64Array(this.memory.buffer, addr, length); },
	int64_t (addr, length = 1) { return new self.BigInt64Array(this.memory.buffer, addr, length); },
	float (addr, length = 1) { return new this.mem.float.typed_array_constructor(this.memory.buffer, addr, length); },
	double (addr, length = 1) { return new this.mem.double.typed_array_constructor(this.memory.buffer, addr, length); },
	long_double (addr, length = 1) { return new self.Uint8Array(this.memory.buffer, addr, length * this.mem.long_double.size); },

	addr2 (name, length = 1) { return this.addr(this.globals[name] || this.exports[name].value, length); },

	getCStringLength (addr)
	{
		const _addr = addr;

		// Find terminating "\0".
		for (let vend = 0; ; ++vend)
		{
			// if (this.char(_addr + vend)[0] === 0)
			if (this.memory_views.UI8[_addr + vend] === 0)
			{
				return vend;
			}
		}

		// return this.exports.__strlen(addr);
	},

	c_string (addr)
	{
		return this.char(addr, this.getCStringLength(addr));
		// return this.memory_views.UI8.subarray(addr, addr + this.getCStringLength(addr));
	},

	c_string2 (addr)
	{
		return this.constructor.convertUint8ArrayToDomString(this.c_string(addr));
		// return this.constructor.convertUint8ArrayToDomString(this.memory_views.UI8.subarray(addr, addr + this.getCStringLength(addr)));
	},

	std_string (addr)
	{
		const result =
			this.char
			(
				this.exports.__getStdStringData(addr),

				this.exports.__getStdStringSize(addr),
			);

		return result;
	},

	std_string2 (addr)
	{
		return this.constructor.convertUint8ArrayToDomString(this.std_string(addr));
	},

	std_vector (addr, type)
	{
		const result =
			this[type]
			(
				this.exports.__getStdVectorData(addr),

				this.exports.__getStdVectorSize(addr),
			);

		return result;
	},

	// std_vector_bool (addr) { return this.std_vector(addr, 'bool'); },
	std_vector_char (addr) { return this.std_vector(addr, 'char'); },
	std_vector_int (addr) { return this.std_vector(addr, 'int'); },
	std_vector_short (addr) { return this.std_vector(addr, 'short'); },
	std_vector_long (addr) { return this.std_vector(addr, 'long'); },
	std_vector_size_t (addr) { return this.std_vector(addr, 'size_t'); },
	std_vector_addr (addr) { return this.std_vector(addr, 'addr'); },
	std_vector_uint8_t (addr) { return this.std_vector(addr, 'uint8_t'); },
	std_vector_int8_t (addr) { return this.std_vector(addr, 'int8_t'); },
	std_vector_uint16_t (addr) { return this.std_vector(addr, 'uint16_t'); },
	std_vector_int16_t (addr) { return this.std_vector(addr, 'int16_t'); },
	std_vector_uint32_t (addr) { return this.std_vector(addr, 'uint32_t'); },
	std_vector_int32_t (addr) { return this.std_vector(addr, 'int32_t'); },
	std_vector_uint64_t (addr) { return this.std_vector(addr, 'uint64_t'); },
	std_vector_int64_t (addr) { return this.std_vector(addr, 'int64_t'); },
	std_vector_float (addr) { return this.std_vector(addr, 'float'); },
	std_vector_double (addr) { return this.std_vector(addr, 'double'); },
	std_vector_long_double (addr) { return this.std_vector(addr, 'long_double'); },

	resizeStdVector (addr, size)
	{
		this.exports.__resizeStdVector(addr, size);
	},

	updateStdVectorData (addr, type, _data)
	{
		this.std_vector(addr, type).set(_data);
	},



	demangleCxxNames ()
	{
		const demangled_name_max_length = 1024;
		const demangled_name_addr = this.exports.__malloc(demangled_name_max_length);

		const exports_demangled_reduced = {};

		const exports_demangled =
			Object.keys(this.exports)
				.reduce
				(
					(exports_demangled, _name) =>
					{
						if (_name.startsWith('_Z'))
						{
							const name = `${ _name }\0`;

							this.memory_views.UI8.set(this.constructor.convertDomStringToUint8Array(name), demangled_name_addr);

							const demangled_name =
								this.constructor.convertUint8ArrayToDomString
								(this.c_string(this.exports.__demangleCxxName(demangled_name_addr)))
									.replace(/, /g, ',');

							if (exports_demangled[demangled_name])
							{
								if (Array.isArray(exports_demangled[demangled_name]))
								{
									exports_demangled[demangled_name].push(this.exports[_name]);

									return exports_demangled;
								}

								const first_entry = exports_demangled[demangled_name];

								exports_demangled[demangled_name] = [ first_entry, this.exports[_name] ];

								return exports_demangled;
							}

							exports_demangled[demangled_name] = this.exports[_name];

							exports_demangled_reduced[demangled_name.replace(/\([^)]*\)/g, '')] = this.exports[_name];
						}

						return exports_demangled;
					},

					{},
				);

		this.exports.__free(demangled_name_addr);

		return { exports_demangled, exports_demangled_reduced };
	},



	// TODO: rename to "getClass".
	Class (name)
	{
		const wasm_wrapper = this;



		class _Class
		{
			static name = name;

			static overloaded = {};



			constructor (input)
			{
				if (typeof input === 'number')
				{
					this.addr = input;
				}
				else if (typeof input === 'string')
				{
					[ this.addr ] = wasm_wrapper.addr2(input);
				}
			}
		}

		self.Object.keys(this.exports_demangled)
			.filter
			(
				member_name => member_name.includes(name),
			)
			.forEach
			(
				member_name =>
				{
					let member_name_trimmed = member_name.replace(`${ name }::`, '').replace(/\((.*)+/g, '');

					// Some objects may be exported twice from wasm. TODO: why does it occur?
					const wasm_object =
						wasm_wrapper.exports_demangled[member_name][0] || wasm_wrapper.exports_demangled[member_name];

					if (typeof wasm_object === 'function')
					{
						const parameters =
							member_name.match(/\((.*)+/g)?.[0].replace(/\(|( )|\)/g, '').split(',');

						if (parameters.length && parameters[0] === '')
						{
							parameters.length = 0;
						}

						// static function
						if (parameters.length === wasm_object.length)
						{
							if (_Class[member_name_trimmed])
							{
								if (!_Class.overloaded[member_name_trimmed])
								{
									_Class.overloaded[member_name_trimmed] = 0;
								}

								++_Class.overloaded[member_name_trimmed];

								member_name_trimmed += `_o${ _Class.overloaded[member_name_trimmed] }`;
							}

							_Class[member_name_trimmed] = wasm_object;
						}

						// non-static function
						else
						{
							if (_Class.prototype[member_name_trimmed])
							{
								if (!_Class.overloaded[member_name_trimmed])
								{
									_Class.overloaded[member_name_trimmed] = 0;
								}

								++_Class.overloaded[member_name_trimmed];

								member_name_trimmed += `_o${ _Class.overloaded[member_name_trimmed] }`;
							}

							// Non-wrapped
							_Class[`proto_${ member_name_trimmed }`] = wasm_object;

							_Class.prototype[member_name_trimmed] =
								function (...args)
								{
									return wasm_object(this.addr, ...args);
								};
						}
					}
					else if (typeof wasm_object === 'object')
					{
						_Class[member_name_trimmed] = wasm_object;
					}
				},
			);

		// Presume New() returns a pointer
		if (_Class.New)
		{
			_Class.NewWrapper = () => new _Class(_Class.New());
		}



		return _Class;
	},
};
